<template>
  <div class="content">
    <h1 class="title">{{ article.title }}</h1>

    <div class="information">
      <span class="aboutMsg">
        <span><img :src="article.userUrl" alt=""></span>
        <span>{{article.username}}</span><span>|</span>
        <span>:&nbsp;{{type}}</span>
        <span></span><span></span><span></span>
      </span>
      <span class="time">{{time}}</span>
    </div>

    <div class="message">
      <v-md-preview :text="article.content"></v-md-preview>
    </div>

    <div class="footer">
      <span>
        
        <div class="tag">
          <span>{{ type }}</span>
        </div>
      </span>
    </div>

    
  </div>
</template>

<script>
export default {
  props: {
    article: {
      type: Object,
      defalut: {
        btype: 1,
        column_id: 0,
        comment_count: 0,
        content: "# \nhtml\n\n margin border padding content\n\nmargin\nborder\npadding\ncontent\n\n\n![Description](https://img-blog.csdnimg.cn/img_convert/b8888fe9de137592c3576280ac84e237.png#pic_center)\n# \ncontentcontentpaddingbordermargin\n\n>= +  +  +  +  +  + \n>= +  +  +  +  +  + \n\n## 1\n![Description](https://img-blog.csdnimg.cn/img_convert/ebbfdeb266fbfb9a434e94f311e48cb7.png#pic_center)\n\ncsswidthheightcontent\nwidthheightcss\n> =margin-left + border-left + padding-left + width + padding-right + border-right + margin-right\n> =margin-top + border-top + padding-top + height + padding-bottom + border-bottom + margin-bottom\n\ndiv background: pink; width: 200px; height: 200px; padding: 10px; margin: 10px; border: 10px solid red;\n=10+10+10+200+10+10+10=260 px,=10+10+10+200+10+10+10 =260 px\n\n![Description](https://img-blog.csdnimg.cn/img_convert/93a7b28d1d4dba8e27b604082200ddb1.png)\n\n## 2ie\n![Description](https://img-blog.csdnimg.cn/img_convert/75720f87592f48efa90d85f35faab231.png#pic_center)\n\niecsswidthheightcontentborder+padding+content\nwidthheightcss\n\n>=margin-left + width + margin-right\n>=margin-top + height + margin-bottom\n\ndiv background: pink; width: 200px; height: 200px; padding: 10px; margin: 10px; border: 10px solid red;\n=10+200+10=220 px,=10+200+10 =220 px\n~~ie!~~",
        create_time: "2021-12-03T13:49:33.000+00:00",
        id: 1877401621,
        identify: 1,
        status: 0,
        subject: 1,
        title: "",
        type: 0,
        up_count: 10,
        update_time: "2021-12-03T13:49:33.000+00:00",
        url: null,
        user_id: -1075408895
      }
    }
  },
  data(){
    return {
      watch: 2000,
      remark: 205,
    }
  },
  computed: {
    time(){
      let t = new Date(this.article.update_time);
      let y=t.getFullYear();let m=t.getMonth()+1;let d=t.getDate();
      return y+'-'+m+'-'+d;
    },
    type(){
      if(this.article.btype===0) return '';
      else if(this.article.btype===1) return '';
      else return '';
    }
  },
  methods: {
    // 
    async showOnePost(id){
      let data ={
        'id': this.id
      }
      const {data: res} = await this.$http.post('/discussPost/showOnePost' , this.qs.stringify(data));
      this.article=res;
    },
  }
}
</script>

